#article-preços{
	background-color: #FAC400;
	padding: 50px 0;
}

#title-preços h3{
	text-align: center;
	margin-bottom: 0;
	padding: 40px 0;
	color: #292828;
	font-family: 'Megrim';
    font-weight: bolder;
    font-size: 50px;
}


#twoImg{
	transform: scaleX(-1);
}

.divFlex{;
	width: 250px;

	display: flex;
	position: relative;
	background-color: #F4F4F4;
	flex-direction: column;
	border-top: 2px solid #292828;
    border-right: 2px solid #292828;
    border-left: 2px solid #292828;
    border-bottom: 6px solid #292828;
    box-shadow: 0px 12px 24px #35363885;
}
.imgTagSeas{
	position: absolute;
	top: 0;
	width: 100%;
	height: 100%;
	border-radius: 50%;
}
#headerPreçoDestaque{
	border: 2px solid #db9208;

	border-radius: 20px;
}

#footerPreçoDestaque{
	border-radius: 20px;
	border: 2px solid #db9208;
}
#divFlexMeio{
	width: 300px;
border-radius: 20px;
    padding: 20px;
}
.corpoPreço img{
	margin-right: 5px;
}

.headerPreço{
	background-color: #FFA700;
	padding: 40px 20px;
	border-bottom: 2px solid #db9208;
}
.headerPreço h4{
	text-transform: uppercase;
	font-size: 25px;
	margin-bottom: 20px;
	color: white;
	text-align: center;
	letter-spacing: 1px;
}

#imgTagDestaque{
	position: absolute;
    top: 0px;
    left: 76.5%;
}

.spanDollar{
	font-size: 40px;
	color: #009688;
	font-weight: 300;
}

.headerPreço p{
	color: black;
	text-align: center;
	font-size:30px;
}
.firstCorpo{
	color: #9b9b9d;
	background-color: #f4f4f4;
	border-top: 2px solid #f1f1f1;
	border-bottom: 2px solid #f1f1f1;
	text-align: center;
	padding: 10px 0;
}

.checkmark-group--icon{
	margin-right: 10px;
	color: #409F1F;
}
.twoCorpo{
	color: #9b9b9d;
	text-align: center;
	padding: 10px 0;
	background-color: #fbfbfb;
}
.imgTag{
	position: absolute;
    top: 0px;
    left: 71.6%;
}

#flexCards{
	display: flex;
	justify-content: center;
	align-items: center;
	margin: 0 auto;
	flex-wrap: wrap;
	position: relative;
	grid-gap: 50px;
	max-width: 1100px;
}


 #btn1{
    background-color: #F4F4F4;
    color:#4D4D4D;
    border-top: 2px solid #292828;
    border-right: 2px solid #292828;
    border-left: 2px solid #292828;
    border-bottom: 6px solid #292828;
    padding: 10px 30px;
    font-weight: bold;
    letter-spacing: 1px;
    border-radius: 10px;
}




#btn1:hover{
    background-color:#d4d4d4;
}

.footerpreço{
	background-color: #FFA700;
	padding: 40px 20px;
	border-top: 2px solid #db9208;
}
.imgZap{
	margin-right: 5px;
}
.btnDiv{
	text-align: center;
}