#divProjetos h2{
	text-align: center;
	font-family: 'Megrim';
    font-weight: bolder;
    color: #F4F4F4;
    font-size: 40px;
    padding-top: 20px;
}
#divProjetos p{
	text-align: center;
	margin-bottom: 30px;
}
#article-origamis{
	
	background-color: #FAC400;
	padding: 40px 0;
}
.containerCards{
	margin: 0 auto;
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-wrap: wrap;
	max-width: 1100px;
}

.containerCards .card{
	position: relative;
	width: 320px;
	height: 320px;
	margin: 15px;
	border-top: 2px solid #292828;
    border-right: 2px solid #292828;
    border-left: 2px solid #292828;
    border-bottom: 6px solid #292828;
    box-shadow: 0px 12px 24px #353638d4;
    overflow: hidden;
}

.containerCards .card .imgBx, .containerCards .card .contentBx{
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

.containerCards .card .imgBx img{
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	object-fit: cover;
}
span{
	color: #F4F4F4;
}
.containerCards .card .contentBx::before{
	content: '';
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: #FAC400;
	transform: scaleX(0);
	transition:  transform 0.5s ease-in-out;
	transform-origin: right;
	transition-delay: 0.5s;
}

.containerCards .card:hover .contentBx::before{
	transform: scaleX(1);
	transition:  transform 0.5s ease-in-out;
	transform-origin: left;
	transition-delay: 0s;
}


.containerCards .card .contentBx{
	display: flex;
	justify-content: center;
	align-items: center;

}

.containerCards .card .contentBx .content{
	position: relative;
	padding: 30px;
	z-index: 1;
	transition: 0.5s;
	transform: translateX(-300px);
	transition-delay: 0;
}

.containerCards .card:hover .contentBx .content{
	transform: translateX(0px);
	transition-delay: 0.5s;
}

.containerCards .card .contentBx .content h3{
	font-size: 1.3em;
	text-transform: uppercase;
	letter-spacing: 1px;
}

.containerCards .card .contentBx .content p{
	font-size: 1em;
	line-height: 1.4em;
	font-weight: 300;
	color: #F4F4F4;
}