#flexBtns{
    display:flex;
    grid-gap:10px;
    margin-top: 20px;
}
 #btn1{
    background-color: #F4F4F4;
    color:#4D4D4D;
    border-top: 2px solid #292828;
    border-right: 2px solid #292828;
    border-left: 2px solid #292828;
    border-bottom: 6px solid #292828;
    padding: 10px 30px;
    font-weight: bold;
    letter-spacing: 1px;
    border-radius: 10px;
}
#pHeaderAbout{
    color: white;
    line-height: 30px;
}
#btn2{
    background-color: #4D4D4D;
    color:white;
    border-top: 2px solid #292828;
    border-right: 2px solid #292828;
    border-left: 2px solid #292828;
    border-bottom: 6px solid #292828;
    padding: 10px 30px;
    font-weight: bold;
    border-radius: 10px;
    letter-spacing: 1px;
}
#btn2:hover{
    background-color:#5e5e5e;
}

#btn1:hover{
    background-color:#d4d4d4;
}
#divAboutHeader{
    padding:10px;
    height: 100%;
}

#titleHeaderAbout{
    font-family:'Megrim';
    font-weight:bolder;
    color: #F4F4F4;
    font-size:60px;
}