.menuMobile{
	display: none;
}

@media (max-width: 800px){
	.menuMobile{
		display: block;
		z-index: 2;
	}
	#chk{
		display: none;
	}
	div.menuMobile label div.menuHidden{
		position: absolute;
    	left: 20px;
    	top: 20px;
    	transition: 0.5s;
    	position: fixed;
    	margin: 0px;
    	animation-name: animationHiddenMenu;
    	animation-duration: 1s;
	}

	#divMenuSpan{
		background-color: #F4F4F4;
		padding: 10px;
		display: flex;
		justify-content: center;
		align-items: center;
		transition: 0.5s;
		border-radius: 10px;
	}

	#spanMenu{
		color: #4D4D4D;
		transition: 0.5s;
		text-transform: uppercase;
		font-weight: bolder;
		margin-right: 5px;
	}

	.imgTagSeas{
		display: none;
	}

	#pAbout{
		text-align: center;
	}
	div#title-preços h3{
		font-size: 35px;
	}

	#divMenuNav{
		position:fixed;
		top: -100%;
		left: 0;
		transition: 1s;
		
		transform-origin: top;
		height: 100%;
		background-color: #FAC400;
		z-index: 1;
		width: 100%;

	}
	#imgAboutHeader{
		display: none;
	}

	#titleHeaderAbout{
		font-size: 45px;
	}

