#menuHeaderFlex{
    display: flex;
    align-items: center;
    padding: 10px;
    justify-content: space-between;
}

#divMenuNav nav ul{
    padding: 0;
    margin: 0;
    list-style-type: none;
}

#divMenuNav nav ul li{
    display: inline-block;
}

#divMenuNav nav ul li a{
    display: block;
    text-decoration: none;
    margin: 0 20px;
    color: #4D4D4D;
    font-size: 13px;
    padding: 15px 0;
    text-transform: uppercase;
    font-weight: 600;
    position:relative;
}

#divMenuNav nav ul li  .afterA::after{
   content: '';
   height: 2px;
   opacity: 0;
   width: 100%;
   right: 0;
   transition: transform .15s,opacity .15s,-webkit-transform .15s;
   position: absolute;
   left: 0;
   top: 100%;
   background-color: #F4F4F4;
}

#divMenuNav nav ul li .afterA:hover::after{
    transform: translateY(-2px);
    transition: transform 0.30s,opacity .15s,-webkit-transform .15s;
    opacity: 1;
}

#divMenuNav nav ul li a#aContact{
    margin: 0 0 0 40px;
    background-color: #F4F4F4;
    color:#4D4D4D;
    border-top: 2px solid #292828;
    border-right: 2px solid #292828;
    border-left: 2px solid #292828;
    border-bottom: 6px solid #292828;
    padding: 10px 30px;
    font-weight: bold;
    letter-spacing: 1px;
    border-radius: 10px;
}

#divMenuNav nav ul li a#aContact:hover{
    background-color:#d4d4d4;
}